<template>
    <div style="margin-left:100px;" v-loading="loading" >
        <!-- element-loading-text=""
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)" -->
        <p style="font-size:30px"></p>
        <el-input v-model="input" placeholder=""  type="textarea" :rows="5" style="width:50%;text-align:center">
        </el-input>
        <div style="display:inline-block;margin-left:20px;">
            <el-button @click="add2(input)" style="margin:10px">
            
            </el-button>
            <br>
            <el-button @click="clear" style="margin:10px"></el-button>

        </div>
        
        <br>
        <div v-for="(item,i) in items" >            
            <p style="">{{i+1}}</p>
            <img :id="'a'+item"/>    
        </div>

    </div>
</template>

<script>

    import { finished } from 'stream';
    export default{

        data(){
            return{
                input:"",
                items:[],
                str:[],
                loading:false,
            }
        },
        
        methods:{
            clear(){
                this.items=[];
                this.input="";
            },
            add2:function (input) {
                if(input){
                    this.loading=true;
                    this.items=[];
                    this.str = input.split((/,\r|,\n|;\r|;\n|;|\r|\n|,/));
                    for(var i = 0; i < this.str.length; i++ ){
                        this.items.push(this.str[i]);
                        console.log(this.str[i]);
                    };
                    setTimeout(() => {
                        this.add3();
                    }, 100);
                } else{
                    this.$message("")
                }
                
            },
            add3(){
                for(var i = 0; i < this.str.length; i++ ){
                    console.log(this.str[i]);
                    JsBarcode("#a"+this.str[i], this.str[i]);
                };
                this.loading=false;
                this.finished();
            },
            finished(){
                if(this.input){
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'success',
                        duration:1500,
                    });
                }
                
            }
            
        },
       
    }
</script>
